html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  /* Busca la clase fijar-body dentro de clases de body */
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  line-height: 1.5;
  color: #000000;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

/* usa el directorio relativo para tomar el _index.scss y de ahi agarra todo lo que tenga @forward dentro de ese archivo*/
h1 {
  text-transform: uppercase;
}

body {
  background-color: white;
}

.header {
  background-color: #4cb8b3;
}
@media (min-width: 768px) {
  .header.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0.2rem 0.2rem 0.2rem #333;
    left: 0;
    top: 0;
  }
}
.header h1 {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  font-size: 2, 4rem;
  color: #ffffff;
  /* El & elimina el espacio en el selector a:hover al compilarlo */
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #fef46d;
}

.prueba {
  text-transform: uppercase;
  font-size: 2.4rem;
  color: #fef46d;
  border-radius: 1rem;
}

.video {
  max-height: 70rem; /* Asigna el tamaño maximo del video */
  min-height: 50rem;
  overflow: hidden; /* Quita el overflow del gradient */
  position: relative; /* Hace que el contenido quede relativa al contenido de .overlay */
}
.video .overlay {
  position: absolute; /* Hace que su posicion sea por encima del contenido de la clase .video */
  background: rgb(126, 2, 195);
  background: linear-gradient(270deg, rgba(126, 2, 195, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 10; /* Manda los elementos hasta el frente para que al inspeccionar el video no tape el contenido */
}
.contenido-video h2 {
  color: #ffffff;
  font-size: 7rem;
  margin: 0;
}
.contenido-video p {
  background-color: #f53756;
  display: inline-block; /* Solo toma el espacio que requiere las letras */
  color: #ffffff;
  padding: 1rem 2rem;
  font-weight: 700;
  font-size: 2.4rem;
}

.sobre-festival {
  padding: 5rem 0 5rem 0;
  /* EJEMPLO DE UN MIXIN DE MEDIA QUERY UTILIZADO CON UN MIXIN DE GRID PARA ALINEARLOS HORIZONTALMENTE */
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  color: #f53756;
  font-weight: 700;
}

/* Section entero */
.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #ffffff;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

/* Contenedor grid */
@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #000000;
}

/* Cuadro entero incluyendo las listas */
.escenario {
  padding-top: 2rem;
  /* Creamos el bloque para la imagen que irá despues del elemento p */
}
.escenario p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

/* Agregamos la imagen dependiendo si tiene clase edm */
.edm .nombre-escenario:after {
  background-image: url("../img/icono_edm.png");
}

/* Agregamos la imagen dependiendo si tiene clase rock */
.rock .nombre-escenario:after {
  background-image: url("../img/icono_rock.png");
}

.bg-amarillo {
  background-color: #fef46d;
}
.bg-amarillo li:nth-child(odd) {
  /* darken es exclusivo de scss */
  background-color: #fdee21;
}

.bg-verde {
  background-color: #4cb8b3;
}
.bg-verde li:nth-child(odd) {
  background-color: #348380; /* darken es exclusivo de scss */
}

/* Listas con horarios */
.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

/* Damos estilo al contenedor */
.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

/* Hacemos los grids para mostrar las imagenes en 2, 3, 4 y 6 columnas dependiendo la resolucion */
.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    /* Las lineas introducidas al hacer el @include se inyectaran en este @content */
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

/* Hacemos el overlay para el modal de la imagen */
.overlay {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  background-color: #ffffff;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-cerrar:hover {
  cursor: pointer;
}

/* Damos estilo al contenedor entero*/
.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #ffffff;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

/* Damos estilos a todos los elementos de cada pase de dia 1 y dia 2 */
.pase {
  color: black;
  /* toma el nombre de la clase padre y el & lo junta con lo que sigue abajo */
  /* Damos estilo al cuerpo del contendo del pase (listas) */
  /* Damos estilo al footer (precios) */
}
.pase .nombre-pase {
  color: #ffffff;
  text-align: center;
  font-size: 2.4rem;
}
.pase-header, .pase-footer {
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 1.6rem;
  margin: 0;
}
.pase-header {
  background-color: #f53756;
}
.pase-body {
  background-color: #ffffff;
}
.pase-body ul {
  /* Reseteamos decoraciones de lista */
  margin: 0;
  padding: 0;
  list-style: none;
  /* Arreglamos los colores de fondo */
}
.pase-body ul li,
.pase-body ul :nth-child(odd) {
  background-color: #ffffff;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #752f97;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #f53756;
  padding: 0.5rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #ffffff;
}