@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  background-color: v.$verde;
  @include m.tablet {
    &.fijo {
      position: fixed;
      width: 100%;
      box-shadow: 0.2rem 0.2rem 0.2rem #333;
      left: 0;
      top: 0;
    }
  }
  h1 {
    color: v.$blanco;
    text-align: center;
    @include m.tablet {
      margin: 0;
    }
  }
  .contenido-header {
    padding: 2rem;
    @include m.tablet {
      display: flex;
      justify-content: space-between;
    }
  }
}

.navegacion-principal {
  @include m.tablet {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
  a {
    display: block;
    text-align: center;
    margin-bottom: 1rem;
    @include m.tablet {
      margin: 0;
    }
    font-size: 2, 4rem;
    color: v.$blanco;
    /* El & elimina el espacio en el selector a:hover al compilarlo */
    &:hover {
      color: v.$amarillo;
    }
  }
}

.prueba {
  @include m.heading(v.$amarillo, true);
}
