@use "base/mixins" as m;
@use "base/variables" as v;

.sobre-festival {
  padding: 5rem 0 5rem 0;
  /* EJEMPLO DE UN MIXIN DE MEDIA QUERY UTILIZADO CON UN MIXIN DE GRID PARA ALINEARLOS HORIZONTALMENTE */
  @include m.tablet {
    @include m.grid(2, 5rem);
  }
}

.contenido-festival {
  margin-top: 3rem;
  @include m.tablet {
    margin-top: 0;
  }
  display: flex;
  flex-direction: column;
  justify-content: center;
  h2 {
    margin-bottom: 2rem;
  }
  .fecha {
    color: v.$rosa;
    font-weight: 700;
  }
}
