@use "base/mixins" as m;
@use "base/variables" as v;

/* Damos estilo al contenedor entero*/
.boletos {
  padding: 5rem 0;
  h3 {
    text-align: center;
    color: v.$blanco;
  }

  .contenedor-boletos {
    @include m.tablet {
      @include m.grid(2, 5rem);
    }
  }
}

/* Damos estilos a todos los elementos de cada pase de dia 1 y dia 2 */
.pase {
  color: black;
  .nombre-pase {
    color: v.$blanco;
    text-align: center;
    font-size: 2.4rem;
  }
  /* toma el nombre de la clase padre y el & lo junta con lo que sigue abajo */
  &-header,
  &-footer {
    padding: 0.5rem;
    p {
      text-align: center;
      color: v.$blanco;
      text-transform: uppercase;
      font-size: 1.6rem;
      margin: 0;
    }
  }
  &-header {
    background-color: v.$rosa;
  }
  /* Damos estilo al cuerpo del contendo del pase (listas) */
  &-body {
    background-color: v.$blanco;
    ul {
      /* Reseteamos decoraciones de lista */
      @include m.resetear-lista;
      /* Arreglamos los colores de fondo */
      li,
      :nth-child(odd) {
        background-color: v.$blanco;
        padding: 1rem;
        border-bottom: 1px solid #e1e1e1;
      }
    }
  }
  /* Damos estilo al footer (precios) */
  &-footer {
    background-color: v.$morado;
    p {
      font-size: 2.4rem;
      font-weight: 700;
    }
  }
}
