@use "base/mixins" as m;
@use "base/variables" as v;

/* Section entero */
.lineup {
  background-color: v.$rosa;
  padding: 5rem 0;

  h3,
  p {
    text-align: center;
    color: v.$blanco;
  }
  .dia {
    text-transform: uppercase;
    margin: 5rem 0;
  }
}

/* Contenedor grid */
.escenarios-contenedor {
  @include m.tablet {
    @include m.grid(2, 5rem);
  }
  p {
    color: v.$negro;
  }
}

/* Cuadro entero incluyendo las listas */
.escenario {
  padding-top: 2rem;

  /* Creamos el bloque para la imagen que irá despues del elemento p */
  p {
    &:after {
      content: "";
      display: block;
      width: 20rem;
      height: 10rem;
      background-size: 15rem;
      background-repeat: no-repeat;
      background-position: center center;
      margin: 2rem auto 0 auto;
    }
  }
}

/* Agregamos la imagen dependiendo si tiene clase edm */
.edm .nombre-escenario:after {
  background-image: url("../img/icono_edm.png");
}

/* Agregamos la imagen dependiendo si tiene clase rock */
.rock .nombre-escenario:after {
  background-image: url("../img/icono_rock.png");
}

.bg-amarillo {
  background-color: v.$amarillo;
  li:nth-child(odd) {
    /* darken es exclusivo de scss */
    background-color: darken(v.$amarillo, 15%);
  }
}
.bg-verde {
  background-color: v.$verde;
  li:nth-child(odd) {
    background-color: darken(v.$verde, 15%); /* darken es exclusivo de scss */
  }
}

/* Listas con horarios */
.calendario {
  @include m.resetear-lista;
  li {
    padding: 2.5rem;
    font-weight: 700;
    span {
      font-weight: 400;
    }
  }
}
